/* reset and default */
*, *::before, *::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a, a:hover {
  color: black;
  text-decoration: none;
}

button {
  border: none;
  outline: none;
  cursor: pointer;
}

html {
  font-family: 'Manrope', sans-serif;
  font-weight: 500;
}
